o, el precio del bien es constante, debido a la naturaleza homogénea del producto y la imposibilidad de las empresas individuales de influir en el precio. Por lo tanto, el ingreso total aumenta de manera proporcional a la cantidad vendida.")]),_vm._m(14),_vm._m(15),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Dado que el precio es constante en un mercado perfectamente competitivo, el ingreso medio siempre es igual al precio del bien. Esto implica que, para cada unidad adicional vendida, el ingreso medio no varía.")]),_vm._m(16),_vm._m(17),_vm._m(18),_vm._m(19),_vm._m(20),_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("En competencia perfecta, el ingreso marginal también es igual al precio del bien. Esto se debe a que cada unidad adicional vendida genera el mismo ingreso que las anteriores, dado el precio constante en el mercado.")]),_vm._m(21),_vm._m(22),_vm._m(23),_vm._m(24),_vm._m(25),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Desde el punto de vista económico, el monopolista enfrenta una curva de demanda que coincide con la del mercado, ya que es el único proveedor del bien o servicio. Esto significa que la cantidad que produce y vende afecta directamente el precio. ")]),_vm._m(26),_c('div',{staticClass:"row align-items-start mb-4"},[_vm._m(27),_c('div',{staticClass:"col-lg-7"},[_c('AcordionA',{attrs:{"tipo":"b"}},[_c('div',{staticClass:"div",attrs:{"titulo":"Puro"}},[_c('p',[_vm._v("Es cuando una empresa es la única existente dentro de una industria. No suele darse este caso en la economía general.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Artificial"}},[_c('p',[_vm._v("Una empresa usa alguna herramienta o medio para seguir posicionado como monopolio y de esta manera no deja entrar al mercado a otras empresas.")])]),_c('div',{staticClass:"div",attrs:{"titulo":"Natural"}},[_c('p',[_vm._v("La empresa monopolista cubre toda la demanda de los consumidores con un costo menor que si hubiera empresas compitiendo y produciendo en e